.Calendar {
  font-family: 'Roboto';
  box-sizing: border-box; }
  .Calendar .grid {
    position: relative;
    float: left;
    width: 80%;
    border-left: 1px solid #ccc;
    border-top: 1px solid #ccc; }
    .Calendar .grid .day {
      position: relative;
      float: left;
      box-sizing: border-box;
      width: 14.285%;
      border-right: 1px solid #ccc;
      border-bottom: 1px solid #ccc;
      padding: 8px;
      height: 150px; }
      .Calendar .grid .day .full {
        font-size: 12px;
        color: #666;
        font-weight: bold; }
      .Calendar .grid .day .content ul {
        padding: 0;
        list-style-type: none;
        font-size: 12px; }
        .Calendar .grid .day .content ul li {
          white-space: nowrap;
          overflow: hidden; }
